/* LOADER */
.loader-container
{
    height: 100vh;
}
.load-div
{
    top: 40%;
	position: fixed;
    text-align: center;
}
.spinner
{
    -webkit-animation: spin 2s linear infinite;
	-moz-animation: spin 2s linear infinite;
	-ms-animation: spin 2s linear infinite;
	-o-animation: spin 2s linear infinite;
    animation: spin 2s linear infinite;
    color: #87F1FF;
}
@keyframes spin
{
    0%
    {
        color: #ff8797;
    }
    16.67%
    {
        color: #ffa387;
    }
    33.33%
    {
        color: #fffb87;
    }
    50%
    {
        color: #adff87;
    }
    66.67%
    {
        color: #87f1ff;
    }
    83.33%
    {
        color: #c587ff;
    }
    100%
    {
        color: #ff8797;
        transform: rotate(360deg);
    }
}

