	border-style: outset;
	border-width: 5px;
	border-radius: 10px;
	display: inline-block;
	vertical-align: top;
	margin-top: 2px;
	margin-right: 2px;
}
.runebutton:hover
{
	color: black;
	background-color: lime;
	border-color: black;
}
.runebutton:active
{
	color: black !important;
	background-color: white !important;
	border-color: black !important;
}

.runebox.complete, .runebutton.complete, .runebutton.complete:hover, .runebutton.complete:active
{
	color: darkgreen !important;
	border-color: darkgreen !important;
	background-color: black !important;
